body.locked {
  overflow: hidden; }

.navbar__container {
  display: flex;
  justify-content: space-between; }
  .navbar__container .logo {
    margin-left: 10px;
    color: #0e2431;
    font-size: 1.2rem; }
  .navbar__container .checkbox {
    position: absolute;
    display: none;
    height: 26px;
    width: 32px;
    top: 20px;
    right: 20px;
    z-index: 5;
    opacity: 0;
    cursor: pointer; }
  .navbar__container .hamburgerLines {
    display: none;
    height: 26px;
    width: 32px;
    position: absolute;
    top: 17px;
    right: 20px;
    z-index: 2; }
    .navbar__container .hamburgerLines .line {
      display: none;
      height: 4px;
      width: 100%;
      border-radius: 10px;
      background: #0e2431; }
    .navbar__container .hamburgerLines .line1 {
      transform-origin: 0% 0%;
      transition: transform 0.4s ease-in-out; }
    .navbar__container .hamburgerLines .line2 {
      transition: transform 0.2s ease-in-out; }
    .navbar__container .hamburgerLines .line3 {
      transform-origin: 0% 100%;
      transition: transform 0.4s ease-in-out; }
  .navbar__container .menu-items {
    display: flex;
    flex-direction: row;
    align-items: center;
    list-style: none; }
    .navbar__container .menu-items .item {
      padding: 30px 20px; }
      .navbar__container .menu-items .item a {
        color: black;
        text-decoration: none; }
      .navbar__container .menu-items .item:hover a {
        font-weight: bolder; }
  .navbar__container .btns {
    margin: auto 0; }
    .navbar__container .btns button {
      padding: 0;
      margin: 0;
      border: 0;
      background-color: transparent;
      border-radius: 0;
      cursor: pointer;
      appearance: none;
      display: inline-block;
      background-color: #0e2431;
      color: white;
      padding: 5px 2px; }

@media (max-width: 767.98px) {
  .navbar__container {
    display: block; }
    .navbar__container .checkbox {
      display: block; }
    .navbar__container .hamburgerLines {
      display: flex;
      flex-direction: column;
      justify-content: space-between; }
      .navbar__container .hamburgerLines .line {
        display: block; }
    .navbar__container input[type="checkbox"]:checked ~ .hamburgerLines .line1 {
      transform: rotate(45deg); }
    .navbar__container input[type="checkbox"]:checked ~ .hamburgerLines .line2 {
      transform: scaleY(0); }
    .navbar__container input[type="checkbox"]:checked ~ .hamburgerLines .line3 {
      transform: rotate(-45deg); }
    .navbar__container .menu-items {
      width: 100%;
      box-shadow: inset 0 0 2000px rgba(255, 255, 255, 0.5);
      transform: translate(-200%);
      display: flex;
      flex-direction: column;
      text-align: center; }
      .navbar__container .menu-items .item {
        margin-bottom: 1.2rem;
        font-size: 1.5rem;
        font-weight: 500; }
    .navbar__container .btns {
      transform: translate(-200%);
      display: flex;
      margin: auto;
      width: 40%;
      flex-direction: column;
      gap: 20px; }
    .navbar__container input[type="checkbox"]:checked ~ .btns {
      transform: translateX(0);
      transition: transform 0.5s ease-in-out; }
    .navbar__container input[type="checkbox"]:checked ~ .menu-items {
      transform: translateX(0);
      transition: transform 0.5s ease-in-out; }
    .navbar__container input[type="checkbox"]:checked ~ .logo {
      display: none; } }
